<template>
    <div>
        <div class="center-card">
            <div class="box_title">
                行业概况 >> 信息简报
            </div>
            <div class="tabStl">
                <input type="text" class="searchStl" placeholder="请输入信息简报关键字搜索">
                <i class="el-icon-search iconStl" @click="requestTableData"></i>
            </div>

            <div class="table_box">
                <div class="table_body" v-for="(item,index) in tableData" :key="index">
                    <span style="width: 200px">
                        <span style="display: inline-block; width: 15px; height: 15px; background-color: skyblue; border-radius: 20px"></span>
                    </span>
                    <span style="width: 1760px">
                        <a :href="item.website" target ="_blank">{{item.title}}</a>
                    </span>
                    <span style="width: 320px">{{item.create_time}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {http} from '@/utils/request'
import Breadcrumb from "@/components/Breadcrumb";
import CloseBtn from "@/components/CloseBtn";
export default {
    data() {
        return {
            tableData: [],
            searchIpt: '',
            activeTab: "1",
        }
    },
    mounted() {
        this.requestTableData()
    },
    methods: {
        requestTableData() {
            let params = {
                title: this.searchIpt
            }
            http.get(
                '/information_briefing/informationBriefingQueryPage',{params}).then(res => {
                this.tableData = res.data.result;
                this.tableData.forEach(item => {
                    item.create_time = item.create_time.slice(0, 10)
                })
            })
        },
    },
    components: {
        Breadcrumb,
        CloseBtn,
    }
}
</script>

<style lang="scss" scoped>
.center-card {
    height: 1050px;
    .box_title {
        font-size: 32px;
        color: #21facd;
    }
    .tabStl {
        position: relative;
        height: 50px;
        margin-bottom: 40px;
        .searchStl {
            width: 500px;
            height: 40px;
            padding-left: 20px;
            position: absolute;
            top: 20px;
            right: 200px;
            background: rgba(74, 129, 169, 0);
            border-radius: 30px;
            border: 1px solid #439799;
            color: #fff;
        }
        .iconStl {
            position: absolute;
            top: 30px;
            right: 230px;
            font-size: 20px;
            cursor: pointer;
        }
    }
    .table_box {
        height: 940px;
        overflow-y: auto;
        .table_body {
            span:first-child {
                text-align: center;
            }
            span {
                height: 70px;
                display: inline-block;
                line-height: 70px;
                font-size: 26px;
                color: #fff;
            }
            border-bottom: 1px solid #439799;
        }
    }
    
}

</style>